// Variables
$primary-color: #333;
$secondary-color: #fff;
$highlight-color: #f39c12; // Color for highlighting the current page
$underline-color: #f39c12; // Color for the underline
$border-color: #ddd; // Color for the border
$border-width-top: 4px; // Width of the top border
$border-width-side: 2px; // Width of the side borders
$font-family: Arial, sans-serif;
$line-height: 1.6;
$header-background: $primary-color;
$header-text-color: $secondary-color;
$footer-background: $primary-color;
$footer-text-color: $secondary-color;

// Base styles
body {
  font-family: $font-family;
  line-height: $line-height;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.content-wrapper {
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
}

header {
  background: $header-background;
  color: $header-text-color;
  padding: 1rem 0;

  nav {
    // Style for navigation bar
    display: flex;
    justify-content: flex-start; // Align items to the left
    padding: 0 2rem; // Add padding for spacing from edges

    ul {
      list-style: none;
      padding: 0;
      margin: 0;
      display: flex;
      align-items: center;

      li {
        margin-right: 20px; // Space between menu items

        a {
          color: $header-text-color;
          text-decoration: none;
          padding: 0.5rem 1rem; // Add padding for clickable area

          &.current {
            font-weight: bold;
            color: $highlight-color; // Highlight color for the current page
          }
        }
      }

      .current a {
        font-weight: bold;
        color: $highlight-color; // Highlight color for the current page
      }
    }
  }
}

main {
  flex: 1;
}

footer {
  background: $footer-background;
  color: $footer-text-color;
  text-align: center;
  padding: 0.1rem 0;

}

// Home Page Specific Styles
.home {
  display: flex;
  justify-content: space-between;
  padding: 2rem;

  .left-side {
    width: 30%;
    padding: 1rem;
    background-color: #f5f5f5; // Background color for the left side
    border-radius: 8px;
    text-align: center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); // Add shadow for better separation

    .profile-photo {
      width: 80%;
      height: auto;
      border-radius: 50%;
      margin-bottom: 1rem; // Space between photo and text
    }

    .profile-info {
      margin-bottom: 1rem;

      h2 {
        margin: 0;
        font-size: 1.5rem;
      }

      p {
        margin: 0;
        font-style: italic;
        color: $primary-color;
      }
    }

    .contact-info {
      h2 {
        margin-bottom: 0.5rem;
      }

      p {
        margin: 0.5rem 0;
      }

      a {
        color: $primary-color;
        text-decoration: none;
      }
    }
  }

  .right-side {
    width: 65%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    height: 100vh; // Ensure it occupies the full viewport height

    .about-box,
    .news-box {
      background-color: #f9f9f9; // Background color for the boxes
      padding: 1rem;
      border-radius: 8px;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); // Shadow for better separation
      border: $border-width-side solid $border-color; // Side borders
      border-top: $border-width-top solid $border-color; // Top border
      height: 50%; // Make each box take up half the available height
      overflow: auto; // Add scroll if content overflows
    }

    .about-box {
      // Styles specific to the about section
      h1 {
        margin-top: 0;
      }
    }

    .news-box {
      h2 {
        margin-top: 0;
      }

      .news-item {
        display: flex;
        align-items: center;
        margin-bottom: 1rem;

        .news-logo {
          width: 50px;
          height: 50px;
          margin-right: 1rem;
        }

        .news-content {
          .news-date {
            color: $primary-color;
            font-size: 0.9rem;
            margin-top: 0.2rem;
          }
        }
      }

      .see-more {
        display: block;
        text-align: center;
        margin-top: 1rem;
        color: $primary-color;
        text-decoration: none;
        font-weight: bold;
        border-top: 1px solid #ddd;
        padding-top: 0.5rem;
      }
      .see-more:hover {
        display: block;
        text-align: center;
        margin-top: 1rem;
        text-decoration: none;
        font-weight: bold;
        border-top: 1px solid #ddd;
        padding-top: 0.5rem;
        color: #3498db;
      }
    }
  }
}

// Heading Styles with Underline
h1, h2, h3, h4, h5, h6 {
  position: relative;
  display: inline-block;
  margin: 0;
  padding-bottom: 0.3rem;
  font-weight: bold;
}

.contact-info {
  // Existing styles...

  .social-media {
    display: flex;
    justify-content: center;
    margin-top: 1rem; // Space above the social media icons

    .social-link {
      margin: 0 0.5rem; // Space between icons

      .social-icon {
        width: 48px; // Adjust size as needed
        height: 48px; // Adjust size as needed
        transition: opacity 0.3s ease;

        &:hover {
          opacity: 0.7; // Add hover effect
        }
      }
    }
  }
}

.news-box {
  padding: 1rem;
  border-radius: 8px;
  background-color: #f9f9f9;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);

  h1 {
    margin-top: 0;
  }
}

.blogs-list, .news-list {
  margin-top: 20px;
}

.blog-item, .news-item {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  padding: 1rem;
  border-bottom: 1px solid #ddd;
}

.news-logo {
  width: 50px; // Adjust size as needed
  height: 50px; // Adjust size as needed
  margin-right: 1rem;
}

.blog-content, .news-content {
  flex: 1;
  h2 {
    margin: 0;
    font-size: 1.2rem;
  }
}

.blog-date, .news-date {
    color: #888;
    font-size: 0.9rem;
  }

.blog-link, .news-link {
  text-decoration: none;
  color: inherit;
}

.blog-link:hover, .news-link:hover {
  color: #3498db; /* 悬停时的颜色变化 */
}  

.post {
  margin-bottom: 40px;
}

.post h1 {
  font-size: 2.5em;
  margin-bottom: 10px;
}

.post-date {
  color: #888;
  font-size: 0.9em;
  margin-bottom: 20px;
}

.post-content {
  font-size: 1.2em;
  line-height: 1.6;
}

.post-content h2, .post-content h3, .post-content h4 {
  margin-top: 20px;
  margin-bottom: 10px;
  border-bottom: 2px solid #3498db;
  padding-bottom: 5px;
}

.post-content p {
  margin-bottom: 20px;
}

.post-content ul {
  margin-left: 20px;
  margin-bottom: 20px;
}

.post-content ul li {
  margin-bottom: 10px;
}

.post-content img {
  max-width: 100%;
  height: auto;
  margin-bottom: 20px;
}